import React from "react";

const ListItem = ({
  caption,
  items,
  setItems,
  index,
  cursor_pos,
  selected,
  setSelected,
  setWin,
  setShowFound,
  setShowError,
  data,
}) => {
  const innerHeight = 651;
  const innerWidth = 1366;

  const onClick = () => {
    const clientHeight = window.innerHeight;
    const clientWidth = window.innerWidth;
    const xDelta = innerHeight - clientHeight;
    const yDelta = innerWidth - clientWidth;
    const character = items[index];
    const position_of_character = data[character];
    let match = false;
    // console.log(cursor_pos.x + xDelta, cursor_pos.y + yDelta);
    // console.log(document.innerHeight, document.innerWidth);
    for (const position of position_of_character) {
      if (
        cursor_pos.x + xDelta === +position.x &&
        cursor_pos.y + yDelta === +position.y
      ) {
        match = true;
        const newState = { ...selected };
        newState[character] = true;
        setSelected(newState);
        setShowFound(true);

        setItems((prevState) => prevState.filter((item) => item !== character));

        if (Object.values(newState).every((item) => item === true)) {
          setWin(true);
        }
      }
    }
    if (!match) {
      setShowError(true);
    }
  };

  return <li onClick={onClick}>{caption}</li>;
};

export default ListItem;
